body{
    background-color:#1f1e1e;
    color: white;
    /* overflow: hidden; */
   }
   h1{
    padding-top: 20px;
    text-align: center;
   }
  
  .todo-wrapper{
  background-color:#353434 ;
  padding: 2%;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
  margin-top: 3%;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0px 5px 7px rgb(57, 56, 56);
  }
  .todo-input{
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid rgb(95, 93, 93);
    padding-bottom: 25px;
    margin-bottom: 25px;
  }
  .todo-lnput-item{
    display: flex;
    align-items: flex-start;
    flex-direction:column ;
    margin-right: 25px;
  }
  .todo-lnput-item label{
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .todo-lnput-item input{
    padding: 10px;
    border: none;
    width: 250px;
  }
  .todo-lnput-item input:focus{
    outline: 2px solid rgb(0, 230, 122);
  }
  .primaryBtn{
    background-color:rgb(0, 230, 122) ;
    color: white;
    border: none;
    padding: 10px;
    margin-top: 25px;
    width: 60px;
    cursor: pointer;
  }
  .primaryBtn:hover{
    background-color:rgb(0, 230, 122);
  }
  
  .secondaryBtn{
    background-color:rgb(63, 65, 64) ;
    color: white;
    border: none;
    padding: 10px;
    margin-top: 25px;
    width: fit-content;
    cursor: pointer;
  }
  .active{
    background-color:rgb(0, 230, 122);
  }
  .todo-list{
    display: flex;
    flex-direction: column;
  }
  .btn-area{
    margin-bottom: 20px;
  }
  
  .todo-list-item{
    background-color: #414040;
    display: flex;
    justify-content: space-between;
    padding: 25px;
    padding-bottom: 10px;
    padding-top: 10px;
    margin-bottom: 10px;
    align-items: center;
    box-shadow: 0px 3px 5px gray;
  
  
  }
  .todo-list-item h3{
    font-size: 25px;
    color: rgb(0, 230, 122);
    margin: 0%;
    font-weight: bold;
  }
  .todo-list-item p {
    font-size: 14px;
    color: rgb(161, 161, 161);
    margin: 0%;
    font-weight: bold;
  }
  .icon{
    font-size: 25px;
    cursor: pointer;
  }
  .check-icon{
    font-size: 25px;
    margin-left: 10px;
    color: rgb(0, 230, 122) ;
  }
  .icon:hover{
    color: tomato;
  }
  .check-icon:hover{
    color: green;
  }
  