.mask{
          width: 100%;
          height: 100vh;
          position: relative;
}
.into-img{
          width: 100%;
          height: 100%;
          background-size: cover;
          background-position: center;
          object-fit: cover;
}
.mask::after{
          content:"";
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          background-color: black;
          opacity: 0.7;
}
.hero{
          height: 100%;
          width: 100%;
}
.hero .content{
          position: absolute;
          transform: translate(-50%,-50%);
          top: 50%;
          left: 50%;
          text-align: center;
}
.hero .content p{
          font-size: 1.4rem;
          font-weight: 200;
          text-transform: uppercase;
}
.hero .content h1{
          font-size:2.5rem;
          padding: 0.6rem 0 1.5rem;
}
 .content .btn{
          margin: 1rem 0.2rem;
}
@media screen and (max-width:640px) {
          .hero .content p{
                    font-size: 1.4rem;
                    
          }
          .hero .content h1{
                    font-size:2rem;
                    
          } 
}